function log(...args: any[]): void {
    if (typeof (console) !== 'undefined' && typeof (document) !== 'undefined') {
        console.log(...args);
        document.querySelector('pre')!.innerHTML += "<br/>" + args.join(" ");
    }
}

const testInputArr: string = getTESTInput();
const inputArr: string = getInput();

const findLows = (inputArr: number[][]) =>
    inputArr.map((lineArr: number[], y: number, wholeArr: number[][]) =>
        lineArr.map((num: number, x: number) => {
            if (y === 0) {
                if (x === 0) {
                    return num < wholeArr[y][x + 1] && num < wholeArr[y + 1][x]
                }
                else if (x === lineArr.length - 1) {
                    return num < wholeArr[y + 1][x] && num < wholeArr[y][x - 1]
                }
                else {
                    return num < wholeArr[y + 1][x] && num < wholeArr[y][x - 1] && num < wholeArr[y][x + 1]
                }
            } else if (y === inputArr.length - 1) {
                if (x === 0) {
                    return num < wholeArr[y][x + 1] && num < wholeArr[y - 1][x]
                }
                else if (x === lineArr.length - 1) {
                    return num < wholeArr[y - 1][x] && num < wholeArr[y][x - 1]
                }
                else {
                    return num < wholeArr[y - 1][x] && num < wholeArr[y][x - 1] && num < wholeArr[y][x + 1]
                }
            }
            else {
                if (x === 0) {
                    return num < wholeArr[y][x + 1] && num < wholeArr[y - 1][x] && num < wholeArr[y + 1][x]
                }
                else if (x === lineArr.length - 1) {
                    return num < wholeArr[y][x - 1] && num < wholeArr[y + 1][x] && num < wholeArr[y - 1][x]
                }
                else {
                    return num < wholeArr[y + 1][x] &&
                        num < wholeArr[y][x - 1] &&
                        num < wholeArr[y][x + 1] &&
                        num < wholeArr[y - 1][x]
                }
            }

        })
    )


const parseInput = (input: string): any =>
    input
        .split("\n")
        .map(line => line.split("").map((e: string): number => parseInt(e)))


const arrInUse =
    getInput();


const lowsArr: boolean[][] =
    findLows(parseInput(arrInUse))
// .map(line => line)

log("a:",
    parseInput(arrInUse)
        .flatMap((line: number[], y: number) =>
            line.map((num: number, x: number): number | boolean => lowsArr[y][x] ? num : false))
        .filter((num: boolean | number) => num !== false)
        .map((e: number) => e + 1)
        .reduce((acc: number, next: number) => acc + next, 0)

)

log("b:",


)


